const galleryBox = document.querySelector('.gallery');

const imageCard = images
  .map(
    image => `<li class="gallery-item">
     <a class="gallery-link" href="${image.original}">
       <img
         class="gallery-image"
         src="${image.preview}"
         alt="${image.description}"
         width="360" height="200"
       />
     </a>
   </li>`
  )
  .join(' ');

galleryBox.innerHTML = imageCard;

galleryBox.style.display = 'flex';
galleryBox.style.flexWrap = 'wrap';
galleryBox.style.justifyContent = 'center';
galleryBox.style.gap = '24px';
galleryBox.style.padding = '0';
galleryBox.style.margin = '0';
galleryBox.style.marginBottom = '40px';
galleryBox.style.listStyle = 'none';

new SimpleLightbox('.gallery a', {
  overlay: true,
  overlayOpacity: 0.9,
  captionsData: 'alt',
  captionDelay: 250,
});